import React, { useState } from "react";
import Carousel from "react-material-ui-carousel";
import { useSelector, useDispatch } from "react-redux";
import { Modal } from "@mui/material";

import "./styles.css";

interface IItems {
  id: number;
  bg: string;
  photo: string;
  isPhoto: boolean;
}

const Item = ({ page, item }) => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState<boolean>(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDragOver = (event) => {
    event.preventDefault();
  };

  const handleDrop = (event) => {
    event.preventDefault();
    //     
    const droppedItem = JSON.parse(event.dataTransfer.getData("text/plain"));
    //       

    dispatch({ type: "ADD_PHOTO", payload: { droppedItem, page } });
  };

  return (
    <>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div
          className="modal"
          style={
            item.bg
              ? { backgroundImage: `url(${item.bg})` }
              : { backgroundImage: "noone" }
          }
        >
          <img src={item.photo} alt="" />
        </div>
      </Modal>
      <div
        className="car_item"
        style={{ backgroundImage: `url(${item.bg})` }}
        onClick={handleOpen}
      >
        <div
          className={item.isPhoto ? "drop-zone dragging" : "drop-zone"}
          onDragOver={handleDragOver}
          onDrop={handleDrop}
        >
          {item.isPhoto ? (
            <img src={item.photo} alt="" />
          ) : (
            <p>   </p>
          )}
        </div>
      </div>
    </>
  );
};

const Car = () => {
  const { items }: any = useSelector((state) => state);

  const dispatch = useDispatch();

  const [index, setIndex] = useState<number>(0);

  const handleChange = (now, previou) => {
    setIndex(now);
    dispatch({ type: "CURRENT_PAGE", payload: now + 1 });
  };

  const addPage = () => {
    dispatch({ type: "ADD_ITEM" });
  };

  return (
    <div>
      <Carousel
        onChange={handleChange}
        indicators={false}
        className="my-carousel"
        navButtonsAlwaysVisible={true}
        animation="fade"
        autoPlay={false}
        height="100%"
      >
        {items.map((item: IItems, i: number) => (
          <Item key={i} item={item} page={index + 1} />
        ))}
      </Carousel>
      <p className="page_num"> {index + 1}</p>
      <div className="add_remove_page">
        <button onClick={addPage}>Add Page</button>
      </div>
    </div>
  );
};

export default Car;
